<template>
  <div v-if="isLoggedIn">
    <div id="a">
      <div class="container">
        <div class="centered-container">
          <h2></h2>
        </div>
        <div id="search_list"></div>
        <table border="1px" align="center" cellspacing="0">
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="user in paginatedUsers" :key="user.userid">
            <td>{{ user.userid }}</td>
            <td>{{ user.username }}</td>
            <td>
              <router-link :to="{ name: 'UserOrderHistory', props:[user.userid]}">
                
              </router-link>
            </td>
          </tr>
        </table>
        <div class="pagination">
          <button @click="goToPrevPage" class="prev"></button>
          <span id="page-info"> {{ currentPage }} </span>
          <button @click="goToNextPage" class="next"></button>
        </div>
      </div>
    </div>
  </div>
  <div v-else class="else">
    <span><a href="/login"></a></span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoggedIn: true, // 
      currentPage: 1,
      users: [
        { userid: 1, username: "user1" },
        { userid: 2, username: "user2" },
        { userid: 3, username: "user3" },
        { userid: 4, username: "user4" },
        { userid: 5, username: "user5" },
        { userid: 6, username: "user6" },
        { userid: 7, username: "user7" },
        { userid: 8, username: "user8" },
        { userid: 9, username: "user9" },
        { userid: 10, username: "user10" },
        { userid: 11, username: "user11" },
        { userid: 12, username: "user12" },
        { userid: 13, username: "user13" },
        { userid: 14, username: "user14" },
        { userid: 15, username: "user15" },
      ], // 
      pageSize: 5,
      filteredUsers: [], //  
    };
  },
  computed: {
    paginatedUsers() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredUsers.slice(start, end);
    },
    totalPages() {
      return Math.ceil(this.filteredUsers.length / this.pageSize);
    }
  },
  methods: {
    goToPrevPage() {
      if (this.currentPage > 1) this.currentPage--;
    },
    goToNextPage() {
      if (this.currentPage < this.totalPages) this.currentPage++;
    },
    fetchUsers() {
      //  AJAX 
    },
  },
  mounted() {
    // 
    //  showSuccessModal
    this.filteredUsers = this.users; // 
    this.fetchUsers();
  },
};
</script>

