<template>
  <div class="success-page">
    <h2></h2>
    <div v-if="SuccessMessage">
      {{ SuccessMessage }}
    </div>
    <div v-if="redirectTo">
      <router-link :to="redirectTo"></router-link>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      SuccessMessage: '',
      redirectTo: null
    };
  },
  created() {
    // 
    this.SuccessMessage = this.$route.query.message || '';
    this.redirectTo = this.$route.query.to || '/';
  }
};
</script>

